<template>
  <div v-if="book" class="template" id="app">
    <div class="template__cover-page">
      <img
        class="template__cover-page--flower-1"
        src="./assets/flower1.png"
        alt=""
      />
      <img
        class="template__cover-page--flower-2"
        src="./assets/flower2.png"
        alt=""
      />
      <div class="template__book-title">{{ book.title }}</div>
      <div class="template__book-img">
        <img :src="book.image" alt="" class="template__book-photo" />
      </div>

      <div class="template__cover-message">
        {{ book.cover_message }}
      </div>
    </div>
    <div class="template__content-page">
      <div
        class="template__content-content"
        v-html="book.content.content"
      ></div>
    </div>
    <div v-for="(message, i) in messages" :key="i + 'ewresewdsf'" class="template__messages my-2">
     <div class="template_message text-center">
       <div class="card">
         <div class="card-body">
           <blockquote class="blockquote mb-0">
             <p>{{ message.message }}</p>
             <footer class="blockquote-footer">{{message.relationship}}<cite title="Source title">{{message.user.name}}</cite></footer>
           </blockquote>
         </div>
       </div>
     </div>
    </div>
    <div class="template__gallery">
      <h2>Gallery</h2>
      <stack :column-min-width="320" :gutter-width="8" :gutter-height="8">
        <stack-item v-for="(item, i) in book_images" :key="i">
          <div class="template__gallery--photo">
            <img :src="item.image" alt="" class="template__gallery--img" />
          </div>
        </stack-item>
      </stack>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Stack, StackItem } from "vue-stack-grid";
export default {
  name: "App",

  components: {
    Stack,
    StackItem,
  },

  data: () => ({
    book: null,
  }),

  computed: {
    book_images() {
      return this.book.book_images;
    },

    messages () {
      return this.book.book_messages
    }
  },

  mounted() {
    this.getBook();
  },

  methods: {
    async getBook() {
      try {
        // read params
        const params = new Proxy(new URLSearchParams(window.location.search), {
          get: (searchParams, prop) => searchParams.get(prop),
        });
        let id = params.id;
        const response = await axios.get(
          `https://mycelebrationbooks.com/api/books/${id}`
        );
        this.book = response.data;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

